const LOGIN_COOKIE_NAME = 'userToken'
const user = 'user'

export function isAuthenticated() {
  return _getCookie(LOGIN_COOKIE_NAME)
}
export function getUser() {
  return _getCookie(user)
}
export function authenticateSuccess(token) {
  _setCookie(LOGIN_COOKIE_NAME, token)

}
export function setUser(token) {
  _setCookie(user, token)

}
export function logout() {
  _setCookie(LOGIN_COOKIE_NAME, '',-1)
  _setCookie(user, '',-1)

}

function _getCookie(name) {
  // console.log(document.cookie)
  let start,end

  if (document.cookie.length > 0) {
    start = document.cookie.indexOf(name + '=')
    if (start !== -1) {
      start = start + name.length + 1
      end = document.cookie.indexOf(';', start)
      if (end === -1) {
        end = document.cookie.length
      }
      return unescape(document.cookie.substring(start, end))
    }
  }
  return ''
}

function _setCookie(name, value,type) {
  let date = new Date().getTime()
  if(type==-1){
    console.log(1)
    document.cookie = name + '=' + escape(value)
  }else{
    console.log(2)

    document.cookie = name + '=' + escape(value ? JSON.stringify(value) + ('?time=' + date) : '')

  }
}
