import { observable, action, computed } from 'mobx'
import api from "./../api/api";
import moment from 'moment';

function getDate(datestr) {

    var temp = datestr.split("-");

    if (temp[1] === '01') {

        temp[0] = parseInt(temp[0], 10) - 1;

        temp[1] = '12';

    } else {

        temp[1] = parseInt(temp[1], 10) - 1;

    }

    //new Date()的月份入参实际都是当前值-1

    var date = new Date(temp[0], temp[1], temp[2]);

    return date;

}
function getYearAndMonthAndDay(start, end) {
    console.log(start)
    var date_all = [], i = 0;
    var startTime = getDate(start);
    var endTime = getDate(end);
    while ((endTime.getTime() - startTime.getTime()) >= 0) {
        var year = startTime.getFullYear();
        var month = (startTime.getMonth() + 1).toString().length == 1 ? "0" + (startTime.getMonth() + 1).toString() : (startTime.getMonth() + 1).toString();
        var day = startTime.getDate().toString().length == 1 ? "0" + startTime.getDate() : startTime.getDate();
        date_all[i] = { time: year + "-" + month + "-" + day, count:0 };
        startTime.setDate(startTime.getDate() + 1);

        i += 1;
    }
    return date_all;
};
class StatisticsStore {
    @observable articleRankInfo = ''
    @observable dailyVOInfo = ''
    @observable pagePvList = ''
    @observable groupByCategory = ''
    @observable groupByCategoryList = ''

    // @observable userInfo= JSON.parse(isAuthenticated().split('?')[0])
    @action setArticleRankInfo = async (type, starttime, endtime, pageNo = 1, pageSize = 5) => {
        let res = await api('getArticleRankInfo', 'post', {}, { type, starttime, endtime, pageSize, pageNo });
        for (let index = 0; index < res.data.list.length; index++) {
            const element = res.data.list[index];
            element.dataIndex = index + 1
        }

        this.articleRankInfo = res.data;



    }
    @action setDailyVOInfo = async (adminId, starttime, endtime) => {
        let res = await api('getDailyVOInfo', 'post', {}, { adminId, starttime, endtime });
        let arr=getYearAndMonthAndDay(moment(starttime).format('YYYY-MM-DD'),moment(endtime).format('YYYY-MM-DD'))
        let arr1=getYearAndMonthAndDay(moment(starttime).format('YYYY-MM-DD'),moment(endtime).format('YYYY-MM-DD'))
        let arr2=getYearAndMonthAndDay(moment(starttime).format('YYYY-MM-DD'),moment(endtime).format('YYYY-MM-DD'))

        getYearAndMonthAndDay(moment(starttime).format('YYYY-MM-DD'),moment(endtime).format('YYYY-MM-DD')).map((val,i) => {
            let array = res.data.listPv ? res.data.listPv : []
            let array1 = res.data.listUv ? res.data.listUv : []
            let array2= res.data.listArticleVo ? res.data.listArticleVo : []

            let str = 0
            for (let index = 0; index < array.length; index++) {
                const element = array[index];
                if (val.time == moment(element.loginDate.split(' ')[0]).format('YYYY-MM-DD')) {
                    console.log(element.count)
                    arr[i].count=element.count
                    // return element.count
                } else {
                    // return 0
                }
            }
            for (let index = 0; index < array1.length; index++) {
                const element = array1[index];
                if (element&&val.time == moment(element.loginDate.split(' ')[0]).format('YYYY-MM-DD')) {
                    console.log(element.count)
                    arr1[i].count=element.count
                    // return element.count
                } else {
                    // return 0
                }
            }
            for (let index = 0; index < array2.length; index++) {
                const element = array2[index];
                if (element&&val.time == moment(element.loginDate.split(' ')[0]).format('YYYY-MM-DD')) {
                    console.log(element.count)
                    arr2[i].count=element.count
                    // return element.count
                } else {
                    // return 0
                }
            }
        })
        res.data.listPv=arr
        res.data.listUv=arr1
        res.data.listArticleVo=arr2

        this.dailyVOInfo =res.data;



    }
    @action setPagePvList = async (adminId, starttime, endtime, pageNo = 1, pageSize = 7) => {
        let res = await api('getPagePvList', 'post', {}, { adminId, starttime, endtime, pageSize, pageNo });

        this.pagePvList = res.data;



    }
    @action setGroupByCategory = async (adminId, starttime, endtime,type=1, pageNo = 1, pageSize = 1000) => {
        let res = await api('getGroupByCategoryId', 'post', {}, { adminId, starttime, endtime,type, pageSize, pageNo });

        this.groupByCategory = res.data;



    }
    
}
export default new StatisticsStore()
