import { observable, action, computed } from 'mobx'

class ArticleStore {
    @observable startTime=''
    @observable endTime=''
    @observable selectId=''
    @observable startTimeVideo=''
    @observable endTimeVideo=''
    @observable selectIdVideo=''
    @action setStartTime=(val)=>{
        this.startTime=val
    }
    @action setEndTime=(val)=>{
        this.endTime=val
    }
    @action setSelectId=(val)=>{
        this.selectId=val
    }
    @action setStartTimeVideo=(val)=>{
        this.startTimeVideo=val
    }
    @action setEndTimeVideo=(val)=>{
        this.endTimeVideo=val
    }
    @action setSelectIdVideo=(val)=>{
        this.selectIdVideo=val
    }
}
export default new ArticleStore()
