import React from 'react';
import { Form, Icon, Input, Button, Checkbox, message, Carousel } from 'antd';
import { authenticateSuccess, isAuthenticated, setUser } from '../../utils/Session'
import NProgress from 'nprogress';

import 'nprogress/nprogress.css';
import BGParticle from '../../utils/BGParticle'
import Loading2 from '../../components/Loading2'
import api from '../../api/api'
import 'animate.css'

import './login.css'
const url = 'https://github.com/zhangZhiHao1996/image-store/blob/master/react-admin-master/bg1.jpg?raw=true'

class Login extends React.Component {
    constructor(props) {
        super(props)
    }
    state = {
        showBox: 'login',   //展示当前表单
        url: '',  //背景图片
        loading: false,
        loading2: false,
    }
    componentWillMount() {
        NProgress.start()

    }
    componentDidMount() {
        // console.log(this.props.isLogin)
        const isLogin = isAuthenticated()
        NProgress.done()
        if (isLogin) {
            this.props.history.go(1)     //当浏览器用后退按钮回到登录页时，判断登录页是否登录，是登录就重定向上个页面
            //this.props.appStore.toggleLogin(false) //也可以设置退出登录
        }
        // this.initPage()
        //preloadingImages(imgs)  //预加载下一个页面的图片，预加载了第二次为什么还会去请求图片资源？
        if (localStorage.getItem('username')) {
            console.log(localStorage.getItem('username'))
            this.props.form.setFieldsValue({ 'userName': localStorage.getItem('username') })
        }
        if (localStorage.getItem('password')) {
            console.log(localStorage.getItem('password'))

            this.props.form.setFieldsValue({ 'password': localStorage.getItem('password') })
        }
    }
    componentWillUnmount() {
        this.particle && this.particle.destory()
        console.log(4321)
    }
    // initPage() {
    //     this.setState({
    //         loading: true
    //     })
    //     // this.props.appStore.initUsers()
    //     this.loadImageAsync(url).then(url => {
    //         this.setState({
    //             loading: false,
    //             url
    //         })
    //     }).then(() => {
    //         //为什么写在then里？id为backgroundBox的DOM元素是在loading为false时才有，而上面的setState可能是异步的，必须等到setState执行完成后才去获取dom
    //         this.particle = new BGParticle('backgroundBox')
    //         this.particle.init()

    //     })
    // }
    switchShowBox = (box) => {
        this.setState({
            showBox: box
        })
    }

    //登录的背景图太大，等载入完后再显示，实际上是图片预加载，
    // loadImageAsync(url) {
    //     return new Promise(function (resolve, reject) {
    //         const image = new Image();
    //         image.onload = function () {
    //             resolve(url);
    //         };
    //         image.onerror = function () {
    //             console.log('图片载入错误')
    //         };
    //         image.src = url;
    //     });
    // }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                api('login', 'get', '', { 'username': values.userName, 'password': values.password }).then(res => {
                    if (res.data.success) {
                        res.data.userToken.roleId = res.data.roleId
                        res.data.userToken.deptId = res.data.deptId
                        if (values.remember) {
                            localStorage.setItem('username', values.userName)
                            localStorage.setItem('password', values.password)

                        } else {
                            localStorage.setItem('username', '')
                            localStorage.setItem('password', '')
                        }
                        authenticateSuccess(res.data.userToken)
                        setUser(res.data)
                        this.props.history.push('/Home')
                    } else {
                        this.error(res.data.errorMessage)
                    }
                })
                console.log('Received values of form: ', values);
            } else {
                this.error()
            }
        });
    };
    error = (val) => {
        if (val) {
            message.error(val)
        } else {
            message.error('This is a message of error')
        }

    };
    render() {
        const { getFieldDecorator } = this.props.form;
        const { showBox, loading } = this.state;
        return (
            <div className="container">
                {loading ?
                    <div>
                        <h3 style={styles.loadingTitle} className='animated bounceInLeft'>载入中...</h3>
                        <Loading2 />
                    </div> :
                    <div>
                        <Carousel dots={false} autoplay>
                            <div>
                                <img src={url} alt="" />
                            </div>
                            <div>
                                <img src={url} alt="" />
                            </div>
                            <div>
                                <img src={url} alt="" />
                            </div>
                            <div>
                                <img src={url} alt="" />
                            </div>
                        </Carousel>
                        {/* <div id='backgroundBox' style={styles.backgroundBox} /> */}
                        <div id='login' className={showBox == 'login' ? 'show' : 'hide'}>
                            <Form onSubmit={this.handleSubmit} className="login-form">
                                <h3 className='loginUser'>管理员登录</h3>
                                <Form.Item>
                                    {getFieldDecorator('userName', {
                                        rules: [{ required: true, message: '请输入您的名称!' }],
                                    })(
                                        <Input
                                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                            placeholder="请输入您的名称"
                                        />,
                                    )}
                                </Form.Item>
                                <Form.Item>
                                    {getFieldDecorator('password', {
                                        rules: [{ required: true, message: '请输入您的密码!' }],
                                    })(
                                        <Input
                                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                            type="password"
                                            placeholder="请输入您的密码"
                                        />,
                                    )}
                                </Form.Item>
                                <Form.Item>
                                    {getFieldDecorator('remember', {
                                        valuePropName: 'checked',
                                        initialValue: true,
                                    })(<Checkbox style={styles.Checkbox}>记住密码</Checkbox>)}
                                    {/* <a className="login-form-forgot" href="">
                                        忘记密码
          </a> */}
                                    <Button type="primary" htmlType="submit" className="login-form-button">
                                        登录
          </Button>

                                </Form.Item>
                            </Form>
                        </div>
                    </div>
                }
            </div>
        );
    }
}
const styles = {
    backgroundBox: {
        position: 'fixed',
        top: '0',
        left: '0',
        width: '100vw',
        height: '100vh',
        // backgroundImage: 'url(https://github.com/zhangZhiHao1996/image-store/blob/master/react-admin-master/bg5.jpg?raw=true)',
        backgroundImage: 'url(https://github.com/zhangZhiHao1996/image-store/blob/master/react-admin-master/bg1.jpg?raw=true)',
        backgroundSize: '100% 100%',
        transition: 'all .5s'
    },
    Checkbox: {
        color: '#fff'
    },
    focus: {
        // transform: 'scale(0.7)',
        width: '20px',
        opacity: 1
    },
    loadingBox: {
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%,-50%)'
    },
    loadingTitle: {
        position: 'fixed',
        top: '50%',
        left: '50%',
        marginLeft: -45,
        marginTop: -18,
        color: '#000',
        fontWeight: 500,
        fontSize: 24
    },
}
const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(Login);

export default WrappedNormalLoginForm;





