import { observable, action, computed } from 'mobx'
import api from "./../api/api";

class AppStore {
  @observable seleteId = 1
  @observable seleteIdLIst = []
  @observable deptName = ''
  @observable countDept = ''
  @observable getCompanyBuild = ''
  @observable deptName = ''
  @observable countDeptEmp = ''
  @observable breadcrumb = []
  @observable deptIdList = []
  @observable deptList = []
  @observable backUserList = []
  @observable backUserListTotal = 0
  @observable listRole = []
  @observable rolePermission = ''
  @observable openkeys = []
  @observable listPermission = []
  @observable roleName = ''
  @observable roleId = ''
  @observable roleList = []
  @observable listType=[{ type: 1, value: '权限类' }, { type: 2, value: '栏目类' }, { type: 3, value: '文章类' }, { type: 4, value: '安全类' }, { type: 5, value: '通用类' }]
  @observable isLoad=true

  @action setCompanyBuild = async () => {
    let res = await api('getCompanyBuild', 'post');
    this.getCompanyBuild = res.data.data;
    this.seleteId = res.data.data.id
    this.seleteIdLIst = [res.data.data.id]
    this.deptName = res.data.data.name
    this.breadcrumb = [{name:res.data.data.name,id:res.data.data.linked}]
    this.setDeptList(res.data.data.id)
    this.setListRole()

    this.setBackUserList()
    this.openkeys = [res.data.data.linked]
    this.setListPermission()
  }

  @action setDeptId = (id) => {
    console.log(id)
    this.seleteId = id
    this.setListRole()
    this.setBackUserList()
    
    this.setDeptList(id)
    this.setRolePermission(id)
  }
  @action setIdList = (idList) => {
    this.seleteIdLIst = idList
    let arr = []
    this.countDept.slice().map(res => {

      for (let index = 0; index < idList.length; index++) {
        const element = idList[index];

        if (element == res.deptId) {
          this.deptName = res.deptName
          let obj={
            name:`${res.deptName} (${res.count}人)`,
            id:res.deptId,
            linked:res.linked
          }
          arr.push(obj)
        }
      }

    })
    this.breadcrumb = arr
  }
  @action setDeptList = async (val = this.seleteId) => {
    let res = await api('getDeptByLevel', 'post', {}, { 'deptId': val });
    this.deptList = res.data.data
  }
  @action setCountDept = async () => {
    let res = await api('countDeptEmp', 'post');
    this.countDept = res.data.data
    let arr = []
    res.data.data.map(res => {
      arr.push(res.deptId)
    })
    this.deptIdList = arr
    let arr1 = []
    this.countDept.slice().map(res => {

      for (let index = 0; index < this.seleteIdLIst.length; index++) {
        const element = this.seleteIdLIst[index];

        if (element == res.deptId) {
          this.deptName = res.deptName
          let obj={
            name:`${res.deptName} (${res.count}人)`,
            id:res.deptId,
            linked:res.linked
          }
          arr1.push(obj)
        }
      }

    })
    this.breadcrumb = arr1
  }
  @action setBackUserList = async (page = 1) => {
    let res = await api('getBackUserByDeptId', 'post', {
      "deptId": this.seleteId,
      "pageNo": page,
      "pageSize": 10
    })
    if (res.data.success) {
      this.backUserListTotal = res.data.total
      for (let index = 0; index < res.data.list.length; index++) {
        const element = res.data.list[index];
        element.points = element.exchangePoints + '/' + element.accumulatedPoints
        for (let i = 0; i < this.listRole.slice().length; i++) {
          const ele = this.listRole.slice()[i];
          if(ele.id==element.roleId){
            element.roleName=ele.role
            break
          }
        }
      }
      
      if (res.data.pageNumber == 1 || res.data.pageNumber == 0) {
        this.backUserList = res.data.list

      } else {
        this.backUserList = this.backUserList.concat(res.data.list)

      }
    }
  }
  @action setListRole = async (val = '') => {
    let res = await api('listRole', 'post', {}, { 'deptId': val })
    this.listRole = res.data.data
    this.roleId = res.data.data[0].id
    let obj = {
      id: res.data.data[0].id,
      value: res.data.data[0].role
    }
    this.roleName = obj
    this.setRolePermission( res.data.data[0].id)
  }
  @action setRoleList = async (val) => {
   if(val){
    let res = await api('listRole', 'post', {}, { 'deptId': val })
    this.roleList = res.data.data
    
   }
  }
  @action setRolePermission = async (val = this.roleId) => {
    if(val){
      let res = await api('getRolePermission', 'post', {}, {
        roleId: val
      })
      console.log(res.data.data)
      this.rolePermission = res.data.data
    }
    
  }
  @action setopenkeys = (val) => {
    this.openkeys = val
  }
  @action setListPermission = async () => {
    let res = await api('listPermission', 'post')
    let arr = []
    // for (let index = 0; index < this.listType.slice().length; index++) {
    //   arr.push({ type:this. listType.slice()[index].type, value: this.listType.slice()[index].value, list: [] })
    //   for (let i = 0; i < res.data.data.length; i++) {
    //     const element = res.data.data[i];
    //     if (this.listType.slice()[index].type == element.type) {
    //       arr[index].list.push(element)
    //     }
    //   }
    // }
    console.log(arr)
    this.listPermission = res.data.data
  }
  @action setRoleId=(val)=>{
    this.roleId=val
    this.setRolePermission(val)
  }
  @action setRoleName=(val)=>{
    this.roleName=val
  }
  @action setIsLoad=(val)=>{
    this.isLoad=val
  }
}

export default new AppStore()