import React from 'react'
import { NavLink ,Link} from 'react-router-dom'
import { logout } from "../../utils/Session";
import { inject, observer } from 'mobx-react/index'

import { Menu ,Icon} from 'antd';
import './index.css'
const list = [
    {
        'title': '文章管理',
        'key': '/Article/Index',
    },
    {
        'title': '视频管理',
        'key': '/Video/Index',

    },
    {
        'title': '反馈管理',
        'key': '/Feedback',

    },
    {
        'title': '用户管理',
        'key': '/Users',

    },
    {
        title: '员工管理',
        key: '/Staff/Dept',

    }, {
        title: '系统配置',
        key: '/System'
    }
]
@inject('headerStore') @observer

class Header extends React.Component {
   constructor(props) {
       super(props);
       console.log(this.props)
      
   }
   componentWillMount(){
       console.log(this.props.data.location)
       if(this.props.data.location.pathname=='/'){
        this.setState({
            current: '/Article/Index',
        });
       }else{
           let str=this.props.data.location.pathname
           list.map(res=>{

                if(str.indexOf(res.key.split('/')[1])!=-1){
                    this.setState({
                        current: res.key
                    });
                }
           })
      
       }
      
   }
    state = {
        current: '/Article/Index',
    };

    handleClick = e => {
        console.log('click ', e);
        this.setState({
            current: e.key,
        });
    };
    setMenuInfo = (item) => {
        return (
            <Menu.Item key={item.key}>
                <NavLink to={item.key} className={item.key==this.props.headerStore.currunt?'link-active':''} activeClassName='link-active'>
                    {item.title}
                </NavLink>
            </Menu.Item>
        )
    }
    signOut=()=>{
        logout()
        console.log(this.props)
        // this.props.router.push({ pathname:'/Login' })
        console.log(document.cookie)
        this.props.data.history.push('/Login')
    }
    render() {
        return (
            <div id='header'>
                <div className="container">
                    <Link className="logo"  to='/Home'>
                        <img src={require("./../../assets/images/logo.png")} alt="" />
                        <p>国家电网江苏...</p>
                    </Link>
                    <Menu onClick={this.handleClick} selectedKeys={[this.props.headerStore.current]} mode="horizontal" overflowedIndicator={<Icon type="menu-unfold" />}>
                        {list.map(item => {
                            return this.setMenuInfo(item)
                        })}
                    </Menu>
                    <div className="signOut">
                        <div onClick={this.signOut}>退出</div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Header