async function api(urlName, type, data, dataType) {
    let arr = []
    // let path='http://192.168.0.106:5000'
    // console.log(apiList)
    let url=apiList[urlName]
    if (type == 'get') {

        if (dataType) {
            Object.keys(dataType).forEach(function (key) {
                arr.push(key + '=' + dataType[key])

            });
            let str = url + '?' + arr.join('&')
            let res = await axios.get(str)
            return res
        } else {
            let res = await axios.get(url)
            return res
        }
    } else {
        if (dataType) {
            Object.keys(dataType).forEach(function (key) {
                arr.push(key + '=' + dataType[key])

            });
            let str = url + '?' + arr.join('&')
            let res = await axios.post(str, data)
            return res
        } else {
            let res = await axios.post(url, data)
            return res
        }
    }
}
export default api