import React from 'react'
import {Spin} from 'antd'
import Loadable from 'react-loadable'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

class LoadingPage extends React.Component {
  //类似github页面加载的那个加载条
  state={
    isLoad:true
  }
  componentWillMount(){
    this.setState({
      isLoad:true
    })
    // NProgress.start()
  }
  componentWillUnmount(){
    this.setState({
      isLoad:false
    })
    // NProgress.done()
  }
  render () {
    return (
      <Spin size="large"  style={this.state.isLoad?styles.show:styles.hide}/>
    )
  }
}
const styles={
  hide:{
    display:'none'
  },
  show:{
    display:'block',
    position: 'fixed',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%,50%)'

  }
}
const LoadableComponent = (component) => {
  return Loadable({
    loader: component,
    loading: ()=><LoadingPage/>
  })
}

export default LoadableComponent