import React from 'react'
import { withRouter, Switch, Redirect } from 'react-router-dom'
import LoadableComponent from '../../utils/LoadableComponent'
import CheckRouter from '../CheckRouter/CheckRouter'

import './index.css'

const Staff = LoadableComponent(() => import('../../views/Staff/Index/Index'))
const Video = LoadableComponent(() => import('../../views/Video/Index/Index'))
const Article = LoadableComponent(() => import('../../views/Article/Index/Index'))
const FeedBack = LoadableComponent(() => import('../../views/FeedBack/FeedBack'))
const User = LoadableComponent(() => import('../../views/User/User'))
const System = LoadableComponent(() => import('../../views/System/System'))
const Home = LoadableComponent(() => import('../../routes/Home/Home'))
const Statistics = LoadableComponent(() => import('../../routes/Statistics/Statistics'))
// const home1 = LoadableComponent(() => import('../../routes/home1/index'))

class Content extends React.Component {
    render() {
        return (
            <div id='contentMian'>
                <Switch>
                    <CheckRouter  path='/Article' component={Article} />
                    <CheckRouter  path='/Video' component={Video} />
                    <CheckRouter  path='/Staff' component={Staff} />
                    <CheckRouter  path='/FeedBack' component={FeedBack} />
                    <CheckRouter  path='/Users' component={User} />
                    <CheckRouter  path='/System' component={System} />
                    <CheckRouter  path='/Statistics' component={Statistics} />
                    {/* <CheckRouter  path='/home1' component={home1} /> */}
                  
                    <CheckRouter  path='/Home' component={Home} />
                    <Redirect  from='/' to='/Home' />
                </Switch>
            </div>
        )
    }
}
export default Content